@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  box-sizing: border-box;
  font-size: 17px;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 30px;
}
.page-body {
  min-height: 100%;
  margin: 0;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
@media (min-width: 768px) {
  .page-body {
    grid-template-rows: 87px 1fr min-content;
  }
}
.page-overlay {
  position: fixed;
  z-index: 50;
  left: 0;
  top: 0;
}
.button {
  display: block;
  box-sizing: border-box;
  padding-top: 12px;
  padding-right: 10px;
  padding-bottom: 12px;
  padding-left: 10px;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  border: 2px solid #444444;
  font: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 16px;
  color: #444444;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #FFFFFF;
  cursor: pointer;
}
.button:hover {
  color: #FFFFFF;
  background-color: #444444;
}
.button:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.button--modal {
  font-weight: 700;
  border-color: #63D1BB;
  color: #FFFFFF;
  background-color: #63D1BB;
}
.button--modal:hover,
.button--modal:focus {
  border-color: #62D1BA;
  background-color: #62D1BA;
}
.content-wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
