.main-nav__toggle {
  position: absolute;
  top: 25px;
  right: 30px;
  z-index: 1;
  display: block;
  width: 30px;
  height: 30px;
  background-color: #FFFFFF;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle-on::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 9px;
  width: 20px;
  height: 2px;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav__toggle-on:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 0 0 rgba(0, 0, 0, 0.3), 0 8px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav__toggle-off {
  top: 25;
  right: 30;
}
.main-nav__toggle-off::before,
.main-nav__toggle-off::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 5px;
  width: 30px;
  height: 2px;
  background-color: #000000;
  transition: 0.25s ease-in-out;
}
.main-nav__toggle-off::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__toggle-off::after {
  transform: rotate(-45deg);
}
.main-nav__toggle-off:active::before,
.main-nav__toggle-off:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
